export default `
<script>
{
    //返回最先出现 searchvalue 的位置
    let fruits=["Banana","Orange","Apple","Mango","Banana","Orange","Apple"];
    let a = fruits.indexOf("Apple");
    let b = fruits.indexOf("Apple",4);
    write(a, b)
}
{
    //lastIndexOf, 从后面往前查找，返回出现的最后一个 searchvalue 的位置
    let fruits=["Banana","Orange","Apple","Mango","Banana","Orange","Apple"];
    let a = fruits.lastIndexOf("Apple");
    let b = fruits.lastIndexOf("Apple",4);
    write(a, b)
}
</script>
`



